import None from "@/components/qy/none.vue"
export default {
	data() {
		return {
			list_empty: false,
			// time:new Date().getTime(),
			getimg:this.$getimg,
			list:[],
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '今日拼团',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '拼团抢先知',
					loadingType: 'more',
					orderList: []
				}
			],
		};
	},
	components:{
		None
	},
	onLoad(){
		this._load()
	},
	methods:{
		_load(){
			this.$api.http.get('pt/get_pt_goods?type=1').then(res=>{
				this.list=res.data
				this.qufen(res.data)
			})
		},
		//活动商品分类 是否今日拼团
		qufen(data){
			const that = this
			let now_time = new Date().getTime()
			let start_time = ''
			for (let k in data) {
				start_time = new Date(data[k].pt.start_time).getTime()
				if(start_time > now_time ){
					that.navList[1].orderList.push(data[k])
					this.list_empty=true
				}else{
					that.navList[0].orderList.push(data[k])
					this.list_empty=true
				}
			}
		},
		jump_pro(id){
			uni.navigateTo({
				url: '/pages/extend-view/productDetail/productDetail?id='+id
			});
		},
		//顶部tab点击
		tabClick(index) {
			console.log(index)
			this.tabCurrentIndex = index;
		},
	},
	onPullDownRefresh() {
		this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
